header {
    height: 10vh;
    line-height: 10vh;
}

header .container {
    padding-top: 15px;
}

header ul li {
    margin-left: 30px;
}
.header .logo
    {
        opacity: 0;
    }

header ul li a {
    transition: 0.5s;
    font-size: 13px;
    letter-spacing: 1px;
    font-weight: 500;
}
header ul li a:hover{
color :#ff014f;
}

.home-btn {
    padding : 18px 25px;
    border-radius: 6px;
    transition: 0.3s all ease;
    cursor: pointer;
    height: fit-content;
    color: #ff014f;
}

.home-btn {
    background: linear-gradient(145deg, #e2e8ec,white);
    box-shadow: 4px 4px 8px #cbcbcb 4px -4px 8px #ffffff;
}
.home-btn:hover{
    background: #ff014f;
    color: white;
}

.open {
    display: none;
}

.header.active {
    height : 12vh;
    position : fixed ;
    top: 0;
    z-index: 1000;
    width: 100%;
    background-color: #ecf0f3;
    box-shadow: -1px 11px 24px -11px rgba(0, 0, 0, 0.75);
    transition: 0.5s all ease;
}

@media (max-width:768px)
{
    header ul {
        padding: 30px;
    }
    .nav-links-mobile {
        position: absolute;
        display: block;
        list-style: none;
        box-shadow: rgba(50,50,93,0.23) 0 50px 100px -20px,rgba(0, 0, 0, 0.3) 0 30px 60px -30px;
    left :0;
    top :0;
    transition: all 0.5s ease-in-out;
    background-color: white;
    z-index: 888;
    }
    header ul li {
        width: 100%;
        transition: 0.5s;
        text-transform: uppercase;
        line-height: 70px;
    }
    header .link {
        display: none;
    }
    .close,.open
    {
        position: absolute;
        top: 20px;
        right: 20px;
        color: #ff014f;
        display: block;
        font-size: 25px;
        z-index: 999;
    }
    .close{
        right: 50%;
    }
    .close .home-btn
    {
        color:#ff014f;
        border-radius: 50%;
        padding :0;
        width :60px;
        line-height: 60px;
    }
    .header.active{
        height: 1vh;
    }
    .header .logo
    {
        opacity: 0;
    }
}

.demo {
    height: 200vh;
}