@import url('https://fonts.googleapis.com/css2?family=Poppins:ital@1&display=swap');

*{
  padding : 0;
  margin : 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body
{
  font-family: 'Poppins', sans-serif;
  background-color: #ecf0f3;
}

.container {
  max-width: 95%;
  margin: auto;
}

.d_flex {
  display: flex;
  justify-content: space-between;
}
.f_flex {
  display: flex;
}

a {
  text-decoration: none;
  color: black;
}

li {
  list-style: none;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

button
{
  border :none;
  outline: none;
  background-color: none;
}

.btn_shadow {
  padding : 18px 25px;
  border-radius: 6px;
  cursor: pointer;
  color: #000;
  transition: 0.5s;
}

.btn_shadow
 {
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb -4px -4px 8px #ffffff;
}

.btn_shadow:hover
{
  background:#ff014f;
  color: white;
  transform: translateY(-5px);
}
.box_shadow {
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
  border-radius: 10px;
}
.row,
.left,
.right {
  width: 50%;
}
.top {
  margin-top: 80px;
}
.mtop {
  margin-top: 40px;
}
.heading 
{
  margin : 50px 0 50px 0;
}

.heading h4 
{
  color: #ff014f;
  letter-spacing: 2px;
  font-weight: 400;
}

.heading h1 {
  font-size: 60px;
  color: #3c3e41;
}

h1{
  color: #3c3e41;
}

.grid
{
  display: grid;
  grid-template-columns: repeat(2,2fr);
  grid-gap: 50px;
}

textarea,
input {
  width: 100%;
  padding: 20px;
  border-radius: 5px;
  outline: none;
  border: 3px solid #e2e8ec;
  margin-bottom: 20px;
}
@media (max-width: 1000px) {
  .grid {
    grid-template-columns: repeat(1,2fr);
  }
  .left,
  .right {
    width: 100%;
  }
}